.app {
  text-align: center;
  display: flex;
  padding: 4rem;
  flex-direction: column;
  align-items: center;
}
.app button{
  width: 5rem;
  margin-bottom: 2rem;
}
.content{
  display: flex;
}
.content form{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 1px solid black;
}
.dados_pessoais{
  width: 50%;
}
.dados_pessoais input{
  width: 70%;
  margin: .5rem 0;
}
.destinos{
  width: 50%
}

.destinos select{
  width: 70%;
  margin-left: 15px;
  margin-top: 1rem;
}
@media only screen and (max-width: 607px){
.app form{
  flex-direction: column;
  align-items: center;
 }
 .app button{
  margin-top: 2rem
 }
 .destinos{
  width: 100%;
 }
 .dados_pessoais{
  width: 100%;
}
}
